 <!--违规下架-->
<template>
  <div>
    <cms-prompt>
      <li>显示本店违规下架的商品列表信息</li>
      <li>可对商品进行重新提交审核、修改及删除等操作</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>商品名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
      </div>
      <div class="query-group">
        <label>SPU编号</label>
        <el-input v-model="queryParams.itemCode" @keyup.native.enter='query'></el-input>
      </div>
      <div class="query-group">
        <label>品牌</label>
        <el-input v-model="queryParams.brandName" @keyup.native.enter='query'></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <section class="cms-batch">
      
      <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
      <cms-button icon="up" type="success" size="small" @click="productReviewPasses()">批量上架</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="itemCode" label="SPU编号" width="160"></el-table-column>
      <el-table-column prop="name" label="商品名称" min-width="340" class-name='is-height'>
        <template slot-scope="scope">
          <div class="product-table">
            <div class="product-img">
              <img :src="showProductImg(scope.row.resources)" :alt="scope.row.name">
            </div>
            <div class="product-desc">
              <div class="product-name">
                {{scope.row.name}}
              </div>
              <div class="product-attr">
                <span v-if="scope.row.brand">品牌： {{scope.row.brand.name}}</span>
                <span>货号： {{scope.row.itemNumber}}</span>
              </div>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="letter" label="店铺名称" min-width="160" class-name='is-height'>
        <template slot-scope="scope">
          <div class="product-brand">
            <div class="brand-title">{{scope.row.store.storeName}}</div>
            <span class="brand-shop" v-if="scope.row.store.selfShop">(自营店铺)</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="分类名称" min-width="115" class-name='is-height'>
        <template slot-scope="scope">
          <div class="product-brand">
            <div class="brand-title">{{scope.row.category.cateName}}</div>
            <div class="brand-category" v-if="scope.row.category.parent">- {{scope.row.category.parent.cateName}}</div>
            <div class="brand-category" v-if="scope.row.category.parent&&scope.row.category.parent.parent">- {{scope.row.category.parent.parent.cateName}}</div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="价格/库存/运费" min-width="115" class-name='is-height'>
        <template slot-scope="scope">
          <div class="product-price-box">
            <div>
              <span class="tag">价格:</span>
              <span class="tag-val">{{scope.row.salePrice}}</span>
            </div>
            <div>
              <span class="tag">库存:</span>
              <span class="tag-val">{{scope.row.inventory}}{{scope.row.ext.unit}}</span>
            </div>
            <div>
              <span class="tag">运费:</span>
              <span class="tag-val">固定运费</span>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="备注" prop="refuseReason" class="is-height" min-width="120">
      </el-table-column>
      <el-table-column label="操作" width='240' fixed="right" align="center">
        <div slot-scope="scope">
          <el-button type='text' @click="productReviewPass(scope.row.id)">提交审核</el-button>
           <el-button  type='text' @click="deleteData(scope.row.id)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
   
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  productPage,
  productRecyleDelete,
  productToCheck
} from '@/api/product/product.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: {
        page: 1,
        size: 10,
        status: 4,
        name: '',
        categoryId: '',
        itemCode: '',
        storeId: '',
        sellGoodsSale: '',
        storeCategoryId: '',
        brandName: '',
        itemNumber: ''
      }
    }
  },
  methods: {
    productReviewPass(id) {
      this.$confirm('是否确定重新提交上架该商品?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          productToCheck({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('提交成功,请耐心等待审核')
              this.$emit('change', 'audit')
              this.getTableData(productPage, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        }).catch(() => {})
    },
    productReviewPasses() { // 批量上架
      if (this.batchStatus) {
        this.$confirm('是否确定重新上架该商品?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            productToCheck({ ids: this.ids }).then(res => {
              if (res.code === 200) {
                this.$successMessage('提交成功,请耐心等待审核')
                this.$emit('change', 'audit')
                this.getTableData(productPage, this.queryParams)
              } else {
                this.$errorMessage(res.message)
              }
            })
          })
          .catch(() => {})
      } else {
        this.$errorMessage('至少选择一条数据')
      }
    },
    deleteData(id) {
      // 这个才是真实的删除方法
      this.$confirm('删除的商品将会放入回收站中，是否确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          productRecyleDelete({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('删除成功')
              this.$emit('change', 'recycle')
              this.getTableData(productPage, this.queryParams)
            } else {
              this.$errorMessage('删除失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    deleteDatas() {
      if (this.batchStatus) {
        this.$confirm('删除的商品将会放入回收站中，是否确定删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            productRecyleDelete({ ids: this.ids }).then(res => {
              if (res.code === 200) {
                this.$successMessage('删除成功')
                this.$emit('change', 'recycle')
                this.getTableData(this.callback, this.queryParams, this.isPage)
              } else {
                this.$errorMessage('删除失败,' + res.message)
              }
            })
          })
          .catch(() => {})
      } else {
        this.$errorMessage('至少选择一条数据')
      }
    },
    updateProduct(id) {
      this.$store.dispatch('clearProductInfo')
      this.$store.dispatch('setCache', false) // 让页面刷新
      this.$store.dispatch('setStep', 'step2')
      this.$routerLink('/product/update', 'update', { productId: id })
    },
    showProductImg(data) {
      if (data.length > 0) {
        const item = data.filter(v => v.isCover === true)
        if (item[0].imageResouce) {
          return this.$cms.filterUrl(item[0].imageResouce.url)
        } else {
          return '/static/error.png'
        }
      } else {
        return '/static/error.png'
      }
    },
    changeData() {
      // 通过ref来触发
      this.getTableData(productPage, this.queryParams)
    }
  },
  created() {
    this.getTableData(productPage, this.queryParams)
  },
  activated() {
    // this.getTableData(productPage, this.queryParams)
  }
}
</script>

<style>
</style>



