<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" v-if="status" ref='productTab'>
      <el-tab-pane label="出售中" name="first">
        <sale ref='sale' @change="eventChange"></sale>
      </el-tab-pane>
      <el-tab-pane label="待审核" name="second" lazy>
        <audit ref="audit"  @change="eventChange"></audit>
      </el-tab-pane>
      <el-tab-pane label="违规下架" name="third" lazy>
        <shelves ref="shelves" @change="eventChange"></shelves>
      </el-tab-pane>
      <el-tab-pane label="回收站" name="fourth" lazy>
        <recycle ref="recycle"  @change="eventChange"></recycle>
      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
import sale from './sale'
import audit from './auditList'
import recycle from './recycle'
import shelves from './shelves'
export default {
  name: 'productList',
  components: {
    sale,
    audit,
    recycle,
    shelves
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {
    eventChange(ev) {
      // 出售中列表状态更新
      if (ev === 'recycle') {
        if (this.$refs['recycle']) {
          this.$refs['recycle'].changeData() // 调用列表更新
        }
      }
      if (ev === 'shelves') {
        if (this.$refs['shelves']) {
          this.$refs['shelves'].changeData() // 调用列表更新
        }
      }
      if (ev === 'sale') {
        if (this.$refs['sale']) {
          this.$refs['sale'].changeData() // 调用列表更新
        }
      }
      if (ev === 'audit') {
        if (this.$refs['audit']) {
          this.$refs['audit'].changeData() // 调用列表更新
        }
      }
    }

  },
  activated() {
    if (this.$route.query.type === 'list') {
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    }
  }
}
</script>

<style>
</style>



